html, body, #root {
  height: 100vh;
  height: -webkit-fill-available;
  max-height: 100vh;
}

main {
  display: flex;
  flex-wrap: nowrap;
  height: 100vh;
  height: -webkit-fill-available;
  max-height: 100vh;
  overflow-x: auto;
  overflow-y: hidden;
}

.sidebar {
  width: 280px;
}

.sidebar a {
  text-decoration: none;
  text-transform: uppercase;
}